import { Option, Select } from "@material-tailwind/react";
import React, { useState } from "react";
import "./App.css";
import Example from "./Example";
import {
  Category,
  mockCheckInQuestions,
} from "./mock-data/mockCheckInQuestions";

const bgColors = [
  "red-400",
  "orange-400",
  "amber-400",
  "yellow-400",
  "green-400",
  "emerald-400",
  "teal-400",
  "sky-400",
  "blue-400",
  "indigo-400",
  "violet-400",
  "purple-400",
  "fuchsia-400",
  "pink-400",
  "rose-400",
];

function App() {
  const [isQuestionClicked, setIsQuestionClicked] = useState<boolean>(false);
  const [navBar, setNavBar] = useState<string>("Team check-in");
  const [bgColor, setBgColor] = useState<string>("from-emerald-400");
  const [value, setValue] = useState<string>("All");

  const handleNavBar = (e: any) => {
    console.log(e.target.innerHTML);
    setNavBar(e.target.innerHTML);
  };

  const handleQuestionClick = () => {
    if (isQuestionClicked) {
      setIsQuestionClicked(false);
    } else {
      setIsQuestionClicked(true);
    }

    const random = Math.floor(Math.random() * bgColors.length);
    setBgColor("from-" + bgColors[random]);
  };

  const getQuestion = () => {
    console.log(value);
    let questions = mockCheckInQuestions;
    if (value === "Fun") {
      questions = mockCheckInQuestions.filter(
        (a) => a.Category === Category.Fun
      );
    }
    if (value === "Work") {
      questions = mockCheckInQuestions.filter(
        (a) => a.Category === Category.Work
      );
    }

    const random = Math.floor(Math.random() * questions.length);
    const question = questions[random];
    return question;
  };

  const renderContent = () => {
    if (navBar === "Team check-in") {
      return (
        <div>
          <div>
            <div className="mt-10 mb-24 grid justify-items-center">
              <button
                className="px-4 py-2 mb-5 font-semibold text-white text-sm bg-blue-500 rounded-full shadow-sm mx-auto transition ease-in-out delay-100 hover:-translate-y-1 hover:scale-150 hover:bg-indigo-500 duration-300"
                onClick={handleQuestionClick}
              >
                Shuffle a question
              </button>
              <div>
                <Select
                  variant="standard"
                  value={value}
                  onChange={setValue}
                  className="text-white"
                >
                  <Option value="All">All</Option>
                  <Option value="Work">Work</Option>
                  <Option value="Fun">Fun</Option>
                </Select>
              </div>
            </div>
            <div className="text-5xl text-center text-white">
              {getQuestion().Question}
            </div>
          </div>
        </div>
      );
    }
    // return Example();
  };

  return (
    <div className="flex flex-col space-y-14">
      <div className={`h-screen pb-10 bg-gradient-to-b ${bgColor}`}>
        <div className="bg-slate-700 p-2 flex justify-around">
          <button
            className={`font-sans text-gray-100 ${
              navBar === "I check-in" ? "text-3xl" : "text-l"
            }`}
            onClick={(e) => handleNavBar(e)}
          >
            I check-in
          </button>
          <button
            className={`font-sans text-gray-100 ${
              navBar == "Team check-in" ? "text-3xl" : "text-l"
            }`}
            onClick={(e) => handleNavBar(e)}
          >
            Team check-in
          </button>
        </div>
        {renderContent()}
      </div>
    </div>
  );
}

export default App;
